<template>
  <transition name="slide">
    <div class="wrapper">
      <div class="header">
        <div class="title">
          输入"
          <span v-text="currentProduct.productName"></span>
          "库存量
        </div>
        <div class="operate">
          <span class="cancel" @click="cancel">取消</span>
          <span class="desc">修改库存</span>
          <span class="save" @click="save">保存</span>
        </div>
      </div>
      <div class="content-wrapper">
        <div class="detail">
          <label>库存</label>
          <!-- 这里的input不采用v-model的原因是：我们只需要获取这个值，避免vuex和v-model踩坑 -->
          <!-- https://vuex.vuejs.org/zh-cn/forms.html -->
          <input type="text" :value="currentProduct.quantity" ref="input"/>
          <span v-text="currentProduct.unitName"></span>
        </div>
      </div>
    </div>
  </transition>
</template>

<script type="text/ecmascript-6">
  import {ERR_CODE} from 'api/config'
  import {getQuery,getLastUrl,deepCopyArray} from 'common/js/util'
  import {mapGetters,mapActions} from 'vuex'
  import {updateProductCatelog} from 'api/purchase'

  export default {
    data () {
      return {

      }
    },
    computed: {
      ...mapGetters([
        'currentProduct',
        'storeList'
      ])
    },
    methods: {
      cancel() {
        this.$router.back()
      },
      showAlert() {
        this.$refs.alert.show()
      },
      save() {
        let id = getLastUrl()
        if(!id){
          return
        }
        let quantity = this.$refs.input.value
        if(isFinite(quantity) && quantity > 0){
          updateProductCatelog(id, {
            quantity
          }).then((res) => {
            if(res.errCode === ERR_CODE){
              let list = deepCopyArray(this.storeList)
              // 这里采用storeList是为了解决返回不刷新数据的问题
              for(let i = 0;i < list.length; i++){
                if(id === list[i].id){
                  list[i].quantity = quantity
                  break
                }
              }
              this.setStoreList(list)
              this.cancel()
            }
          })
        } else {
          this.$alert.show({'message':'库存必须大于0'})
        }
      },
      ...mapActions([
        'setStoreList',
        'setCurrentProduct'
      ])
    },
    components: {
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"

  .slide-enter-active, .slide-leave-active
    transition all 0.3s

  .slide-enter, .slide-leave-to
    transform translate3d(100%, 0, 0)

  .wrapper
    position absolute
    left 0
    right 0
    top 0
    bottom 0
    z-index 1
    background $color-background
    .header
      position fixed
      height 100px
      top 0
      left 0
      right 0
      background $color-header-background
      display flex
      flex-direction column
      .title
        display flex
        justify-content center
        align-items flex-end
        color #333
        font-size $font-size-small-x
        font-weight bold
        height 34px
      .operate
        display flex
        flex 1
        align-items center
        span
          height 100%
          display flex
          align-items center
          &.cancel
            width 60px
            margin-left 15px
            font-size $font-size-medium
            color #999
          &.desc
            flex 1
            justify-content center
            font-size $font-size-large
            color #333
            font-weight bold
          &.save
            width 60px
            margin-right 15px
            font-size $font-size-medium
            color #eb4747
            justify-content flex-end
    .content-wrapper
      position absolute
      left 0
      right 0
      top 100px
      bottom 0
      z-index 1
      background $color-background
      display flex
      justify-content center
      .detail
        margin-top 10px
        height 54px
        display flex
        align-items center
        background #fff
        width 100%
        label
          margin-left 15px
          font-size $font-size-medium
          color #333
        input
          display flex
          flex 1
          height 100%
          box-sizing border-box
          padding 0 16px
          font-size $font-size-large
          color #eb4747
          outline none
          text-align center
        span
          margin-right 15px
          font-size $font-size-medium
          color #666


</style>



